// : C
@import '../modules';

// pagination  
div.pagination{
  display: flex;
  justify-content: center;
  // arrow   
  button.ar{
    width: 2rem;
    height: 3rem;
    //   , 
    &.--l{
      margin-left: 8px;
      transform: rotate(180deg);
    }
    //   ,
    &.--r{
      margin-right: 8px;
    }
    &:hover,
    &.--able{
      svg path{
        fill: #797979;
        transition: fill 0.3s;
      }
    }
  }

  // pagination 
  ol.pagination-nums{
    display: inline-flex;
    align-items: center;
    margin: 0 16px;
    li button{
      width: 3.6rem;
      height: 3rem;
      color: $font_color-797979_grey;
    }
    li.now button{
      color: $color-2C99FF_azure;
      font-weight: 700;
    }
  }

}
// pagination commu 
div.pagination.commu{
  transform: translateY(-1rem);
  ol li.now button{
    color: $color-0AB4E2_commu-01;
  }
}
